/* //////////////////////// */

.main-container-presentacion {
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 0;
    margin-top: 50px;
    animation-name: about-card;
    animation-duration: 1s;
    animation-timing-function: linear;
}
@keyframes about-card {
    from{
    transform: translateY(300%);
    opacity: 0;
    }
    to{
    transform: translateY(600);
    opacity: 1;
    }
    }

.container-presentacion{

    position: relative;
    width: 400px;
    height: 650px;
    background: #040404;
    box-shadow: 0 15px 18px rgba(179, 179, 179, .6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    overflow: hidden;
    z-index: -1;
    font-size: .9rem;
    color: wheat;
}

.profile {
    width: 180px;
    height: 180px;
    background-image: url('https://media-exp1.licdn.com/dms/image/C4E03AQGjKeJbCpSxkg/profile-displayphoto-shrink_800_800/0/1630512736947?e=1642636800&v=beta&t=phzoErRg5rmYNdFpL9H30Y487RlctoWGz9FgJspbnJI');
    background-size: cover;
    border-radius: 50%;
    border: 3px solid #dc7df3;
    transform: translateY(-10px);
}

.layer {
    position: absolute;
    top: 19%;
    width: 100%;
    height: 3px;
    background:#dc7df3;
    transition: .7s ease;
    z-index: -1;
    overflow: hidden;
}

.details {
    position:absolute;
    text-align: center;
    color: #dc7df3;
    transition: .4s ease;
    transform: translateY(-80px);
    font-size: 18px;
}

.skills {
    display: flex;
    justify-content: center;
    height: 300px;
    width: 400px;
    margin: 0px;
    transform: translate(0px, 40px);
    
}

.skills ul{
    font-size: 1.5rem;
    display: flex;
    list-style: none;
    width: 300px;
    height: 300px;
    justify-content: center;
    padding-left: 0px;
    gap: 10px;
    flex-wrap: wrap;
}

.skills li{
    display: flex;
     width: 90px;
    height: 30px; 
    justify-content:center ;
    align-items: center;
    opacity: 0;
    transition: .4s ease;
    font-size: 14px;
    /* background: chartreuse; */
    border-radius: 30px;
    border: 1px solid ;
    color: #040404;
}

.btn-presentacion a{
    display: block;
    text-decoration: none;
    width: 200px;
    height: 50px;
    border: 1px solid #dc7df3;
    border-radius: 35px;
    text-align: center;
    line-height: 50px;
    color: #dc7df3;
    letter-spacing: 2px;
    transition: .4s ease;
    transform: translateY(10px);
}

.container-presentacion:hover .layer {
    height: 100%;
}

.container-presentacion:hover .details {
    color: #040404;
}

.container-presentacion:hover .btn-presentacion a {
    border-color: #040404;
    color: #040404;
}

.container-presentacion:hover .skills li{
    opacity: 1;
    color: #040404;
}

.divAbout-mensaje {
    width: 950px;
    height: 700px;
    position: absolute;
    top: 110px;
    transform: translate(460px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    animation-name: about-trans;
    animation-duration: 1s;
    animation-timing-function: linear;
}
.divNav {
    animation-name: about-trans;
    animation-duration: 1s;
    animation-timing-function: linear;
}

@keyframes about-trans {
    from{
    transform: translateY(-300%);
    opacity: 0;
    }
    to{
    transform: translateY(600);
    opacity: 1;
    }
    }
.divAbout-mensaje h1 {
color: #dc7df3;
}
.divAbout-mensaje p {
    font-size: 25px;
    text-align:center;
   font-style: italic;
    line-height: 40px;
}
.divAbout-mensaje span a {
    color: #dc7df3;
    text-decoration: none;
    }



@media (max-width: 1880px) {
    .divAbout-mensaje {
        transform: translate(360px);
    }
}
@media (max-width: 1665px) {
    .divAbout {
        display: none;
    }
    .divAbout-mensaje {
        transform: translate(160px);
    }
}
@media (max-width: 1276px) {
    .divAbout-mensaje {
        transform: translate(0px);
    }
}
